import React from "react";
import {Button} from "../common/components/ui/button";
// import Particles from "react-particles-js";

const LandingPage = (props) => {
  return (

    <div>
      <Button
        variant="outlined"
        size="medium"
        onClick={() => props.navigate("/addpt")}
      >
        Add Parttime
      </Button>
    </div>

  );
};

export default LandingPage;
